.gallery-movies {
  width: 1024px;
  display: flex;
  flex-wrap: wrap;
  justify-content: left;
  margin: 0 auto 0;
}

.photo-card {
  margin-top: 32px;
  margin-left: 10px;
  width: 309px; 
  height: 100%;
}

.img-item {
  display: flex;
  justify-content: center;
  border: 1px gray;
  width: 100%;
  height: 200px;
  border-radius: 10px;
}
.image{
  border-radius: 5px;
}

.photo-card:hover {
  box-shadow: 10px 10px 8px 2px rgba(0, 0, 0, 0.3);
  border-radius: 5px;
  transform: scale(1.05);
  transition-duration: 500ms
}

a {
  display: block;
  margin: 10px auto;
  flex-basis: calc(100% / 5 - 20px);
  justify-content: center;
  border-width: 1px;
  border-color: gray;
  text-decoration: none;
}

.info {
  width: 100%;
  height: 40px;
  // display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;

  color: rgb(44, 45, 46);
  border-radius: 0 0 5px 5px;


}

